body {
  margin: 0;
  padding: 1em;
  font-family: sans-serif;
  background: #dbd1c6;
}

.game-status {
  display: block;
  position: absolute;
  top: 10%;
  left: 10%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  color: #919191;
}

.main-field {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.value-0 {
  fill: #ffffff;
}
.value-2 {
  fill: #eee4da;
}
.value-4 {
  fill: #ede0c8;
}
.value-8 {
  fill: #f2b179;
}

.value-16 {
  fill: #f59563;
}
.value-32 {
  fill: #f67c5f;
}
.value-64 {
  fill: #f65e3b;
}
.value-128 {
  fill: #edcf72;
}

svg g polygon {
  stroke: #bbada0;
  stroke-width: 1;
  transition: all 0.3s;
}

svg g.new-item polygon {
  stroke: #a5998d;
  size: 100px;
  stroke-width: 1.2;
  transition: all 0.1s;
  transform: scale(1.05);
}

svg g text {
  font-size: 0.3em;
  fill: #776e65;
}

svg g.value-8 text {
  font-size: 0.3em;
  fill: #ffffff;
}
svg g.value-16 text {
  font-size: 0.3em;
  fill: #ffffff;
}
svg g.value-32 text {
  font-size: 0.3em;
  fill: #ffffff;
}
svg g.value-64 text {
  font-size: 0.3em;
  fill: #ffffff;
}
svg g.value-128 text {
  font-size: 0.3em;
  fill: #ffffff;
}

svg path {
  fill: none;
  stroke: hsl(60, 20%, 70%);
  stroke-width: 0.4em;
  stroke-opacity: 0.3;
  stroke-linecap: round;
  stroke-linejoin: round;
}
